import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    AppGlobals.initialize(mapOf("title" to "Mohamed Azeem"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.azeem.portfolio.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("arrow-up", com.azeem.portfolio.components.widgets.ArrowUpStyle)
        ctx.theme.registerStyle("page-content", com.azeem.portfolio.utils.PageContentStyle)
        ctx.theme.registerStyle("nav-header", com.azeem.portfolio.utils.NavHeaderStyle)
        ctx.theme.registerStyle("hover-bg", com.azeem.portfolio.utils.HoverBgStyle)
        ctx.theme.registerStyle("hover-bg-clickable",
                com.azeem.portfolio.utils.HoverBgClickableStyle)
        ctx.theme.registerStyle("share-button", com.azeem.portfolio.utils.ShareButtonStyle)
        ctx.theme.registerStyle("hover-cursor", com.azeem.portfolio.utils.HoverCursorStyle)
        ctx.theme.registerStyle("footer", com.azeem.portfolio.utils.FooterStyle)
        ctx.theme.registerStyle("back-to-top-button",
                com.azeem.portfolio.utils.BackToTopButtonStyle)
        ctx.theme.registerStyle("sub-head-line-text",
                com.azeem.portfolio.utils.SubHeadLineTextStyle)
        ctx.theme.registerStyle("section-title", com.azeem.portfolio.utils.SectionTitleStyle)
        ctx.theme.registerStyle("section-description",
                com.azeem.portfolio.utils.SectionDescriptionStyle)
        ctx.theme.registerStyle("profile-image", com.azeem.portfolio.utils.ProfileImageStyle)
        ctx.theme.registerStyle("hover-profile-image-art",
                com.azeem.portfolio.utils.HoverProfileImageArtStyle)
        ctx.theme.registerStyle("about-profile-image",
                com.azeem.portfolio.utils.AboutProfileImageStyle)
        ctx.theme.registerStyle("profile-heading", com.azeem.portfolio.utils.ProfileHeadingStyle)
        ctx.theme.registerStyle("about-section-heading",
                com.azeem.portfolio.utils.AboutSectionHeadingStyle)
        ctx.theme.registerStyle("designation", com.azeem.portfolio.utils.DesignationStyle)
        ctx.theme.registerStyle("about-section-designation",
                com.azeem.portfolio.utils.AboutSectionDesignationStyle)
        ctx.theme.registerStyle("about-text", com.azeem.portfolio.utils.AboutTextStyle)
        ctx.theme.registerStyle("resume-button", com.azeem.portfolio.utils.ResumeButtonStyle)
        ctx.theme.registerStyle("play-store-button", com.azeem.portfolio.utils.PlayStoreButtonStyle)
        ctx.theme.registerStyle("na-play-store-button",
                com.azeem.portfolio.utils.NAPlayStoreButtonStyle)
        ctx.theme.registerStyle("skill-icon", com.azeem.portfolio.utils.SkillIconStyle)
        ctx.theme.registerStyle("resume-icon", com.azeem.portfolio.utils.ResumeIconStyle)
        ctx.theme.registerStyle("skill-title", com.azeem.portfolio.utils.SkillTitleStyle)
        ctx.theme.registerStyle("navigation-menu", com.azeem.portfolio.utils.NavigationMenuStyle)
        ctx.theme.registerStyle("surface", com.azeem.portfolio.utils.SurfaceStyle)
        ctx.theme.registerStyle("hover-image", com.azeem.portfolio.utils.HoverImageStyle)
        ctx.theme.registerStyle("hover-image-art", com.azeem.portfolio.utils.HoverImageArtStyle)
        ctx.theme.registerStyle("hero-section", com.azeem.portfolio.utils.HeroSectionStyle)
        ctx.theme.registerStyle("home-section", com.azeem.portfolio.utils.HomeSectionStyle)
        ctx.theme.registerStyle("about", com.azeem.portfolio.utils.AboutStyle)
        ctx.theme.registerStyle("employer-text", com.azeem.portfolio.utils.EmployerTextStyle)
        ctx.theme.registerStyle("certificate-link-text",
                com.azeem.portfolio.utils.CertificateLinkTextStyle)
        ctx.theme.registerStyle("employer-location-text",
                com.azeem.portfolio.utils.EmployerLocationTextStyle)
        ctx.theme.registerVariant("-circle", com.azeem.portfolio.utils.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", com.azeem.portfolio.utils.UncoloredButtonVariant
        )
        ctx.theme.registerKeyframes("fade-in-key-frames",
                com.azeem.portfolio.components.widgets.FadeInKeyFrames)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        com.azeem.portfolio.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
